import React, { useState, useContext } from "react";
import Groups from "./Groups";
import Courses from "./Courses";
import Assignments from "./Assignments";
import Audition from "./Audition";
import Statistics from "./Statistics";
import { AuthContext } from "../context/AuthContext";
import "./TeacherDashboard.css";

const TeacherDashboard = () => {
    const [selectedSection, setSelectedSection] = useState("groups");
    const [isMenuOpen, setIsMenuOpen] = useState(false); //   
    const { auth, logout } = useContext(AuthContext);

    return (
        <div className="dashboard">
            {/* -    */}
            <button className="menu-burger" onClick={() => setIsMenuOpen(!isMenuOpen)}>
                
            </button>

            {/*   */}
            <aside className={`dashboard-menu ${isMenuOpen ? "open" : ""}`}>
                <div className="user-info">
                    <div className="avatar"></div>
                    <div className="user-details">
                        <p className="user-name">{auth?.username || "User"}</p>
                        <button className="logout-button" onClick={logout}>
                            Log Out
                        </button>
                    </div>
                </div>
                <nav>
                    <ul>
                        <li
                            className={selectedSection === "groups" ? "active" : ""}
                            onClick={() => {
                                setSelectedSection("groups");
                                setIsMenuOpen(false); //    
                            }}
                        >
                            Groups
                        </li>
                        <li
                            className={selectedSection === "courses" ? "active" : ""}
                            onClick={() => {
                                setSelectedSection("courses");
                                setIsMenuOpen(false);
                            }}
                        >
                            Courses
                        </li>
                        <li
                            className={selectedSection === "assignments" ? "active" : ""}
                            onClick={() => {
                                setSelectedSection("assignments");
                                setIsMenuOpen(false);
                            }}
                        >
                            Assignments
                        </li>
                        <li
                            className={selectedSection === "audition" ? "active" : ""}
                            onClick={() => {
                                setSelectedSection("audition");
                                setIsMenuOpen(false);
                            }}
                        >
                            Audition
                        </li>
                        <li
                            className={selectedSection === "statistics" ? "active" : ""}
                            onClick={() => {
                                setSelectedSection("statistics");
                                setIsMenuOpen(false);
                            }}
                        >
                            Statistics
                        </li>
                    </ul>
                </nav>
            </aside>

            {/*   */}
            <main className="dashboard-content">
                <div className="content-container">
                    {selectedSection === "groups" && <Groups />}
                    {selectedSection === "courses" && <Courses />}
                    {selectedSection === "assignments" && <Assignments />}
                    {selectedSection === "audition" && <Audition />}
                    {selectedSection === "statistics" && <Statistics />}
                </div>
            </main>
        </div>
    );
};

export default TeacherDashboard;
